<template>
  <div class="d-flex" :class="this.$store.state.menuToggle" id="wrapper">
    <TheSidemenu />
    <div id="page-content-wrapper">
      <TheTopmenu />
      <div class="container-fluid p-1">
        <p class="fs-5 my-2 text-start">
          {{ this.$route.meta.title }}
        </p>
        <div v-if="!completeCourses">
          <i class="fas fa-spinner fa-spin fs-1 mt-3"></i>
        </div>
        <div v-else class="row row-cols-2 g-0">
          <template v-for="item in completeCourses" :key="item.id">
            <div class="col">
              <router-link
                class="text-dark text-decoration-none"
                :to="{
                  name: 'Products',
                  params: { productId: item.id, buyed_before: 1 },
                }"
              >
                <div
                  class="row g-0 bg-light m-1 m-sm-2 align-items-center rounded shadow"
                >
                  <div class="col-12 col-sm-2">
                    <img
                      :src="
                        API_STORAGE_URL + item.thumbnail
                      "
                      class="w-100"
                    />
                  </div>
                  <div
                    class="col-12 my-2 col-sm-8 fs-6 fw-bold text-decoration-none"
                  >
                    {{ item.name }}
                  </div>
                  <div class="col-12 col-sm-2 mb-2 mb-sm-0">
                    <a class="btn btn-theme btn-sm"> جلسات </a>
                  </div>
                </div>
              </router-link>
            </div>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TheSidemenu from "../../components/TheSidemenu";
import TheTopmenu from "../../components/TheTopmenu";
import ArefApi from "../../libs/api.js";
import utils from "../../libs/utils";

export default {
  name: "CompleteCourses",
  created() {
    this.arefApi = new ArefApi();
    this.utils = new utils();
    this.completeCoursesOfUser();
  },
  components: {
    TheSidemenu,
    TheTopmenu,
  },
  data() {
    return {
      completeCourses: null,
      API_STORAGE_URL: process.env.VUE_APP_API_STORAGE_URL,
    };
  },
  methods: {
    completeCoursesOfUser() {
      this.arefApi
        .completeCoursesOfUser()
        .then((resp) => {
          this.completeCourses = resp.data;
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
  },
};
</script>

